.workspace #nr,.workspace .eq-block-grid,.workspace .eq-block-guides{position:absolute;height:486px;top:17px;left:27px}
.workspace {
	.edit-workspace {
		transform-origin: 0px 0px 0px;
		height: 100%;
		width: 100%;
		overflow: hidden;
	}
	.container {
		position: absolute;
		height: 520px;
		width: 374px;
		top: 50%;
		left: 50%;
		margin-top: -260px;
		margin-left: -187px;
		background-color: yellow;
	}
	.nr {
		width: 320px;
	}
}
